.App {
  text-align: center;
}

body {
  margin: 0;
  font-family: "Arial";
  background-image: linear-gradient(to bottom, var(--bg), var(--darkbg));
  color: var(--text);
  overflow-x: hidden;
}

h1 {
  color: var(--text);
  font-size: 2.5rem;
  padding-bottom: 1rem;
}

h2 {
  color: var(--text);
  font-size: 2rem;
}

h3 {
  color: var(--text);
  font-size: 1.5rem;
}

p {
  color: var(--text);
  font-size: 16px;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  color: var(--highlight);
}

a:active {
  text-decoration: none;
}

.link-wrapper {
  text-decoration: none;
  color: var(--text);
}

.link-wrapper:hover,
.link-wrapper:active {
  text-decoration: none;
  color: var(--text);
}

.page {
  position: relative;
  justify-content: center;
  align-items: center;
  margin-top: 8rem;
  margin-bottom: 4rem;
  color: var(--text);
}

.section {
  color: var(--text);
  margin-top: 4rem;
}

.custom-button {
  color: var(--text);
  background-color: var(--pop);
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  cursor: pointer;
  line-height: 20px;
  outline: none;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  text-align: center;
  text-decoration: none;
  transition: color 100ms;
  touch-action: manipulation;
}

.custom-button:hover {
  background-color: var(--highlight);
}

/* Navbar */

.nav-container {
  padding: auto;
}

.navbar {
  background-color: var(--navbar);
  transition: all 0.3s ease-out 0s;
  box-shadow: 0px 10px 10px 0px var(--shadow);
  backdrop-filter: blur(15px);
  height: 3.5rem;
}

.navbar-toggler {
  position: relative;
  background-color: transparent;
  border-color: transparent;
}

.navbar-toggler span {
  display: block;
  background-color: var(--highlight);
  height: 4px;
  width: 27px;
  margin-top: 5px;
  margin-bottom: 5px;
  transform: rotate(0deg);
  left: 0;
  opacity: 1;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
  outline: none;
}

/* Changes hamburger menu to x */

.navbar-toggler span:nth-child(1),
.navbar-toggler span:nth-child(3) {
  transition: transform 0.35s ease-in-out;
  transition: transform 0.35s ease-in-out;
}

.navbar-toggler:not(.collapsed) span:nth-child(1) {
  position: absolute;
  left: 12px;
  top: 10px;
  transform: rotate(135deg);
  opacity: 0.9;
}

.navbar-toggler:not(.collapsed) span:nth-child(2) {
  height: 12px;
  visibility: hidden;
  background-color: transparent;
}

.navbar-toggler:not(.collapsed) span:nth-child(3) {
  position: absolute;
  left: 12px;
  top: 10px;
  transform: rotate(-135deg);
  opacity: 0.9;
}

.navbar-brand {
  color: var(--text);
  height: 100%;
}

.navbar-nav .nav-link,
.navbar-nav .nav-link:focus,
.navbar-nav .nav-link:active {
  color: var(--text);
  padding-right: 1rem;
  padding-left: 1rem;
}

.navbar-nav .nav-link:hover {
  color: var(--highlight);
  text-decoration: none;
}

.logo {
  height: 3.5rem;
  width: auto;
}

.navbar-nav .nav-item {
  position: relative;
  margin-left: 20px;
}

.navbar-nav .nav-item a {
  font-weight: 400;
  transition: all 0.3s ease-out 0s;
  position: relative;
  z-index: 1;
}

.navbar-nav .nav-item a::after {
  content: "";
  position: relative;
  display: block;
  height: 5px;
  width: 0;
  border-radius: 16px;
  background: var(--highlight);
  bottom: 1px;
  left: 0;
  z-index: -1;
  transition: all 0.3s ease-out 0s;
}

.navbar-nav .nav-item a:hover::after {
  width: 100%;
}

@media (max-width: 767px) {
  .navbar {
    font-size: 1.4rem;
    height: auto;
  }

  .nav-link {
    padding: 0.5rem 1rem;
  }

  .navbar-nav .nav-item a::after {
    display: none;
  }

  .logo {
    height: 48px;
  }
}

/* Footer */

.footer {
  background-color: var(--black);
  bottom: 0;
  padding-top: 10px;
  padding-bottom: 8px;
}
.footer-name {
  color: var(--text);
  text-align: center;
}

.footer-links {
  text-align: center;
}

.footer-icons {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  padding: 0 1em;
  text-align: right;
}

.footer h3 {
  font-size: 1em;
  color: var(--text);
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  padding: 0 1em;
  text-align: left;
}

/* Home */

.home-body {
  color: var(--text);
  padding-top: 3rem;
  padding-left: 2rem;
  padding-right: 2rem;
  font-size: 1.2em;
  text-align: center;
}

.home-social {
  justify-content: center;
  padding-top: 1rem;
  display: inline-block;
  position: relative;
  padding-inline-start: 0;
}

.home-social-icons {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  text-align: center;
  font-size: 1.2em;
  line-height: 2em;
  background: rgba(255, 255, 255, 0.972);
  border-radius: 50%;
  transition: 0.5s;
}

.home-social-icons::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: var(--pop);
  transition: 0.5s;
  transform: scale(0.9);
  z-index: -1;
}

.home-social-icons:hover::before {
  transform: scale(1.1);
  box-shadow: 0 0 15px var(--pop);
}

.home-social-icons:hover {
  color: var(--highlight);
  box-shadow: 0 0 5px var(--pop);
  text-shadow: 0 0 2px var(--pop);
}

.social-icons {
  display: inline-block;
  padding: 0 1rem;
}

.icon-color {
  color: var(--darkbg);
}

.avatar {
  justify-content: center;
}

@media (max-width: 767px) {
  .avatar {
    padding-top: 2em;
    padding-bottom: 2em;
  }
}

/* Home buttons */
.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  margin: 4rem auto;
}

.image-link {
  width: 25%;
  aspect-ratio: 1;
  display: flex;
}

.image-button {
  display: flex;
  width: 100%;
  background-size: cover;
  background-position: center;
  align-items: center;
  justify-content: center;
  color: var(--text);
  font-size: 20px;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  border: none;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  transition: transform 0.3s ease-in-out;
}

.image-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.image-button span {
  z-index: 2;
}

.image-button:hover {
  transform: scale(1.1);
}

.image-button:hover::before {
  background-color: rgba(0, 0, 0, 0); /* Reduce overlay darkness on hover */
}

@media (max-width: 767px) {
  .button-container {
    flex-direction: column;
  }

  .image-link {
    width: 80%;
    height: 30vh;
  }
}

/* Projects */
.project-card {
  display: flex;
  align-items: center;
  text-align: left;
  background-color: var(--black);
  border-radius: 8px;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
  margin: 4vh auto;
  padding: 2rem;
}

.project-card:hover {
  box-shadow: 0 0 16px var(--highlight);
}

.project-card img {
  margin-right: 2rem;
  object-fit: cover;
  width: 40%;
}

.project-card-content {
  margin: 0;
  padding: 0;
  width: 60%;
}

.projects-card-content button {
  margin-top: 10px;
}

.project-header {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  color: var(--text);
}

.project-header h1 {
  font-size: 3em;
  padding-bottom: 16rem;
  padding-right: 12vw;
}

.display {
  margin-top: 2rem;
  margin-bottom: 4rem;
  text-align: center;
  color: var(--text);
}

@media (max-width: 767px) {
  .project-card {
    flex-direction: column;
    align-items: flex-start;
    text-align: center;
    margin: 4vh auto;
    max-width: 90%;
  }
  .project-card img {
    width: 100%;
    margin: 0 auto;
  }

  .project-card-content {
    margin-top: 16px;
    width: 100%;
  }

  .project-header h1 {
    padding-right: 8vw;
  }

  .parallax {
    background-size: 100vh auto;
  }
}

/* Parallax scrolling */
.parallax {
  background-image: url(./assets/parallax.gif);
  width: 100%;
  height: 100vh;
  padding-top: 70px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

/* Art */

.art-card {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  margin-bottom: 1.5rem;
}

.art-image {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease;
}

.art-card:hover .artwork-image {
  transform: scale(1.1);
}

.art-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  color: var(--text);
  padding: 10px;
  text-align: center;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out;
}

.art-card:hover .art-caption {
  transform: translateY(0);
}

/* About */

.about-section {
  display: flex;
  padding: 0 10vw;
  margin-bottom: 2rem;
  justify-content: center;
}

.about-section p {
  text-align: left;
}

.skills-section {
  justify-content: center;
}

.skill-block {
  font-size: 1rem;
  margin: 2rem 1rem;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid var(--highlight);
  border-radius: 4px;
  aspect-ratio: 1;
  box-shadow: 0 4px 8px var(--shadow);
  overflow: hidden;
  transition: all 0.3s ease 0s;
}

.skill-block:hover {
  box-shadow: 0 0 8px var(--highlight);
}

.skill-icon {
  font-size: 5rem;
  margin-bottom: 0.5rem;
}

.about-link {
  color: var(--pop);
  text-decoration: none;
}

.timeline {
  position: relative;
  margin: 0 auto;
  width: 80%;
  max-width: 1200px;
}

.timeline-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  margin: 2rem auto;
  text-align: left;
}

/* Connector between timeline-items */
.timeline-item::before {
  content: "";
  position: absolute;
  top: -2rem; /* Same as padding so it connects to the item before and after it */
  bottom: -2rem; /* Same as padding so it connects to the item before and after it */
  width: 4px;
  background-color: var(--highlight);
}

.timeline-item:first-child::before {
  top: 50%;
  bottom: 0;
}

.timeline-item:last-child::before {
  top: 0;
  bottom: 50%;
}

/* Connector line from timeline to timeline-item */
.line-connector {
  position: relative;
  height: 4px;
  width: 5%;
  background-color: var(--highlight);
}

.timeline-content {
  display: flex;
  align-items: center;
  background-color: var(--black);
  border-radius: 8px;
  padding: 1rem;
  position: relative;
  box-shadow: 0 4px 8px var(--shadow);
  transition: all 0.3s ease-in-out 0s;
}

.timeline-content:hover {
  box-shadow: 0 0 8px var(--highlight);
}

.timeline-text {
  margin: 0 1rem;
}

.timeline-content .company-logo {
  width: 6rem;
}

.timeline-content h4 {
  font-weight: 400;
}

.timeline-content p {
  margin: 0.3rem 0 0;
  font-size: 0.9rem;
  color: var(--text-light);
}

@media (max-width: 768px) {
  .about-column {
    margin-bottom: 2rem;
  }

  .skill-block {
    margin: 1rem 0.5rem;
  }

  .timeline::before {
    display: none;
  }

  .timeline-item {
    width: 100%;
    text-align: left;
    margin: 2rem auto;
  }

  .timeline-item::before {
    display: none;
  }

  .line-connector {
    display: none;
  }

  .timeline-content .company-logo {
    margin: 0 1rem;
  }
}
